const footerNavigationList = document.querySelector('.footer-navigation__list');


if (containerWidth <= 768){
    footerNavigationList.addEventListener('click', (e) =>{
        e.preventDefault();
        const self = e.target;
        const footernNavigationSublist = self.nextElementSibling;


        if(self.closest('.footer-navigation__link.active')){
            self.classList.remove('active');
            footernNavigationSublist.classList.remove('show');
            footernNavigationSublist.style.minHeight = `${footernNavigationSublist.scrollHeight}px`;
        }
    })

}
